<template>
  <div class="container">
    <h2 class="text-center mt-5">Vue Todo App</h2>

    <div class="d-flex">
      <input v-model="task" type="text" placeholder="Enter task" class="form-control">
      <button @click="submitTask" class="btn btn-warning rounded-0">SUBMIT</button>
    </div>

    <table class="table table-bordered mt-5">
      <thead>
        <tr>
          <th scope="col">Task</th>
          <th scope="col" class="text-center">Status</th>
          <th scope="col" class="text-center">Edit</th>
          <th scope="col" class="text-center">Delete</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in tasks" :key="index" :class="{ 'task-completed': item.completed }">
          <td>
            <span :style="{ 'text-decoration': item.completed ? 'line-through' : 'none' }">{{ item.name }}</span>
          </td>
          <td>
            <input type="checkbox" v-model="item.completed" @change="updateTaskStatus(index)" />
          </td>
          <td>
            <div class="text-center pointer" @click="updateTask(index)">
              <span class="fa fa-pen"></span>
            </div>
          </td>
          <td>
            <div class="text-center pointer" @click="deleteTask(index)">
              <span class="fa fa-trash" style="color: red;"></span>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: 'TodoApp',
  data() {
    return {
      task: '',
      tasks: []
    }
  },

  mounted() {
    this.loadTasksFromLocalStorage(); 
  },

  methods: {
    submitTask() {
      if (this.task.length === 0) return;

      this.tasks.push({
        name: this.task,
        completed: false
      });

      this.saveTasksToLocalStorage(); 

      this.task = '';
    },

    deleteTask(index) {
      this.tasks.splice(index, 1);
      this.saveTasksToLocalStorage(); 
    },

    updateTask(index) {
      this.task = this.tasks[index].name;
    },

    updateTaskStatus() {
      this.saveTasksToLocalStorage(); 
    },

    saveTasksToLocalStorage() {
      localStorage.setItem('tasks', JSON.stringify(this.tasks));
    },

    loadTasksFromLocalStorage() {
      const savedTasks = localStorage.getItem('tasks');
      if (savedTasks) {
        this.tasks = JSON.parse(savedTasks);
      }
    }
  }
}
</script>

<style scoped>
.pointer {
  cursor: pointer;
}

.task-completed {
  text-decoration: line-through;
}
</style>
